


















	var page = 0,totalpage=0;
	import mGroupbuyList from '@/components/m-groupbuy-list'
	export default {
		data() {
			return {
				// page:0,//当前页数
				// 附近门店
				groupsellList:[]
			}
		},
		components: {
			mGroupbuyList
		},
		methods:{
				getGroupsellList(){
					uni.showLoading({});
					if(totalpage&&page > totalpage){
						uni.showToast({"title":"已经加载全部", icon:"none"});
						return ;
					}
					this.mPost('/server/p/group/products',{
						start:page,
						length:20
					}).then(res=>{
						if(res.code=1){
							if(res.data){
								let data = res.data;
								if(data.list){
									totalpage=data.pages;
									var newsList = data.list;
									this.groupsellList = this.groupsellList.concat(newsList);
									uni.hideLoading();
									page++;
// 									console.log(data.list);
// 									this.groupsellList = data.list;
								}
							}
						}
					}).catch(err=>{
						console.log(err);
					});
				},
				//跳转到商家
				goStore(){
					uni.navigateTo({
						url:"/pages/store/store"
					})
				}
		},
		// 加载更多
		onReachBottom(){
			this.getGroupsellList();
		},
		//下拉刷新
		onPullDownRefresh : function(){
			// 重置分页及数据
			page = 1;
			this.groupsellList =[];
			this.getGroupsellList();
		},
		onLoad(){
			page = 1;
			groupsellList:[];
			this.getGroupsellList();
		}
	}
